import { assert } from '../../../utils/assert';
import { buildChildSet } from './childSet';
import { contains, clone, map, safeGet } from '../../../utils/obj';
import { NamedNode, Node } from './Node';
import { PRIORITY_INDEX } from './indexes/PriorityIndex';
import { KEY_INDEX } from './indexes/KeyIndex';
import { SortedMap } from '../util/SortedMap';
import { Index } from './indexes/Index';

let _defaultIndexMap: IndexMap;

const fallbackObject = {};

/**
 *
 * @param {Object.>} indexes
 * @param {Object.} indexSet
 * @constructor
 */
export class IndexMap {
 /**
 * The default IndexMap for nodes without a priority
 * @type {!IndexMap}
 * @const
 */
 static get Default(): IndexMap {
 assert(fallbackObject && PRIORITY_INDEX, 'ChildrenNode.ts has not been loaded');
 _defaultIndexMap = _defaultIndexMap || new IndexMap(
 {'.priority': fallbackObject},
 {'.priority': PRIORITY_INDEX}
 );
 return _defaultIndexMap;
 }

 constructor(private indexes_: { [k: string]: SortedMap | /*FallbackType*/object },
 private indexSet_: { [k: string]: Index }) {
 }

 /**
 *
 * @param {!string} indexKey
 * @return {?SortedMap.}
 */
 get(indexKey: string): SortedMap | null {
 const sortedMap = safeGet(this.indexes_, indexKey);
 if (!sortedMap) throw new Error('No index defined for ' + indexKey);

 if (sortedMap === fallbackObject) {
 // The index exists, but it falls back to just name comparison. Return null so that the calling code uses the
 // regular child map
 return null;
 } else {
 return sortedMap;
 }
 }

 /**
 * @param {!Index} indexDefinition
 * @return {boolean}
 */
 hasIndex(indexDefinition: Index): boolean {
 return contains(this.indexSet_, indexDefinition.toString());
 }

 /**
 * @param {!Index} indexDefinition
 * @param {!SortedMap.} existingChildren
 * @return {!IndexMap}
 */
 addIndex(indexDefinition: Index, existingChildren: SortedMap): IndexMap {
 assert(indexDefinition !== KEY_INDEX,
 'KeyIndex always exists and isn\'t meant to be added to the IndexMap.');
 const childList = [];
 let sawIndexedValue = false;
 const iter = existingChildren.getIterator(NamedNode.Wrap);
 let next = iter.getNext();
 while (next) {
 sawIndexedValue = sawIndexedValue || indexDefinition.isDefinedOn(next.node);
 childList.push(next);
 next = iter.getNext();
 }
 let newIndex;
 if (sawIndexedValue) {
 newIndex = buildChildSet(childList, indexDefinition.getCompare());
 } else {
 newIndex = fallbackObject;
 }
 const indexName = indexDefinition.toString();
 const newIndexSet = clone(this.indexSet_);
 newIndexSet[indexName] = indexDefinition;
 const newIndexes = clone(this.indexes_);
 newIndexes[indexName] = newIndex;
 return new IndexMap(newIndexes, newIndexSet);
 }


 /**
 * Ensure that this node is properly tracked in any indexes that we're maintaining
 * @param {!NamedNode} namedNode
 * @param {!SortedMap.} existingChildren
 * @return {!IndexMap}
 */
 addToIndexes(namedNode: NamedNode, existingChildren: SortedMap): IndexMap {
 const newIndexes = map(this.indexes_, (indexedChildren: SortedMap, indexName: string) => {
 const index = safeGet(this.indexSet_, indexName);
 assert(index, 'Missing index implementation for ' + indexName);
 if (indexedChildren === fallbackObject) {
 // Check to see if we need to index everything
 if (index.isDefinedOn(namedNode.node)) {
 // We need to build this index
 const childList = [];
 const iter = existingChildren.getIterator(NamedNode.Wrap);
 let next = iter.getNext();
 while (next) {
 if (next.name != namedNode.name) {
 childList.push(next);
 }
 next = iter.getNext();
 }
 childList.push(namedNode);
 return buildChildSet(childList, index.getCompare());
 } else {
 // No change, this remains a fallback
 return fallbackObject;
 }
 } else {
 const existingSnap = existingChildren.get(namedNode.name);
 let newChildren = indexedChildren;
 if (existingSnap) {
 newChildren = newChildren.remove(new NamedNode(namedNode.name, existingSnap));
 }
 return newChildren.insert(namedNode, namedNode.node);
 }
 });
 return new IndexMap(newIndexes, this.indexSet_);
 }

 /**
 * Create a new IndexMap instance with the given value removed
 * @param {!NamedNode} namedNode
 * @param {!SortedMap.} existingChildren
 * @return {!IndexMap}
 */
 removeFromIndexes(namedNode: NamedNode, existingChildren: SortedMap): IndexMap {
 const newIndexes = map(this.indexes_, function (indexedChildren: SortedMap) {
 if (indexedChildren === fallbackObject) {
 // This is the fallback. Just return it, nothing to do in this case
 return indexedChildren;
 } else {
 const existingSnap = existingChildren.get(namedNode.name);
 if (existingSnap) {
 return indexedChildren.remove(new NamedNode(namedNode.name, existingSnap));
 } else {
 // No record of this child
 return indexedChildren;
 }
 }
 });
 return new IndexMap(newIndexes, this.indexSet_);
 }
}



